@import "../mixin";

.navWrapper{
  width: 20%;
  min-width: 250px;

}
.nav{
  @include blockWithShadow(200px, 600px);
  padding: 20px;
  margin-bottom: auto;

}
.userPhoto{
  @include flexBox(center, center);
  flex-direction: column;


  img{
    width: 80%;
    margin-bottom: 30px;
    min-width: 130px;
  }
  h1{
    font-weight: 500;
    font-size: 25px;
    text-align: center;
  }
  span{
    margin-top: 10px;
    color: #5b5b5b;
    font-size: 18px;
  }
}

nav{
  position: relative;
  margin-top: 40px;

  a{
    display: block;
    padding: 5px;
    margin: 10px 5px;
    color: #393a3a;
    text-decoration: none;
    font-size: 20px;
    width: 85%;
    //border: 1px solid white;

    &:hover{
      //background-color: #f0f0f1;
      transition: .5s;
      transform: translateX(10px);
      scale: 1.05;
    }
  }
}
.new{
  width: 30px;
  height: 30px;
  background-color: rgb(204, 16, 3);
  border-radius: 30px;
  @include flexBox(center, center);
  color: white;
  position: absolute;

}
.countMessages{
  top: 50px;
  left: 180px;
}
.countFriends{
  top: 95px;
  left: 180px;
}